.resume {
  background-color: rgba(245,247,241,0.9);
  padding: 1em;
  width: 100%;
  max-width: 960px;
}

.resume > * {
  margin: -0.75em 0 2em;
}

.columns {
  margin-left: 0;
  margin-right: 0;
}

@media screen and (max-width: 768px) {
  .columns .column.has-text-centered {
    text-align: left !important;
  }

  .columns .column.has-text-right {
    text-align: left !important;
  }
}

.content {
  padding: 0 0.75rem;
  margin-top: -0.75rem;
}

.heading .icon.is-small {
  display: inline-block;
  margin: -0.2em 0.5em 0 0;
  vertical-align: middle;
}

.button.is-borderless {
  padding: 0;
  border: none;
  background: none;
  min-width: 1.5em;
  min-height: 1.5em;
}
